.backgroundContainer {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.backgroundVideo {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(8px);
    z-index: -1;
}

.overlayContent {
    position: relative;
    z-index: 1;
    height: 100%;
    position: relative;
    padding: 0 calc(var(--page-padding) / 2)  0 calc(var(--page-padding) * 2);
    display: flex;
    justify-content: center;
    align-items: center;
}

.mainContent {
    position: relative;
    padding: 0 var(--page-padding);
    background-color: transparent;
}


@media (max-width: 768px) {
    .backgroundContainer {
        overflow: scroll;
    }
    .overlayContent {
        margin-top: 60px;
        height: auto;
        padding: var(--spacing-md);
        flex-direction: column-reverse;
        align-items: flex-start;
    }
}